.recovered{
    display:block;
    margin: 20px auto 20px auto;
    padding: 20px;
    border: 5px solid rgba(255, 255, 255, 0.5);
    background-color: rgba(138, 166, 78, 0.712);
    text-align: center;
    text-transform: uppercase;
    border-radius: 2%;
    width: 50vw;
    transition: 0.2s;
}
.recovered:hover{
    background-color:rgba(138, 166, 78, 0.904);
}
.recovered h1{
    color: rgba(255, 255, 255, 0.904);
    margin: 20px 0; 
}
.recovered p {
    color: rgba(255, 255, 255, 0.808);
    font-size: 30px;
    margin: 15px 0;
}
.recovered span{
    font-weight: bold;
}
@media (max-width: 800px){
    .recovered{
        margin: 10px auto 10px auto;
        padding: 10px;
        width: 98vw;
    }
    .recovered h1{
        margin: 10px 0; 
        font-size: 18px;
       
    }
  }