* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
  }
  body {
    background-image: url(./img2.png);
    background-position: center;
    background-size: cover;
  }
  .App{
    margin: 0 auto;
    width: 70vw;
    background-color:rgba(68, 65, 57, 0.61);
    min-height: 90vh;
    border-radius: 5%;
    text-align: center;
  }
  @media (max-width: 800px ){
    .App{
      width: 100vw;
      min-height: 50vh;
    }
  }