.infected{
    display:block;
    margin: 20px auto 20px auto;
    padding: 20px;
    border: 5px solid rgba(255, 255, 255, 0.507);
    background-color: rgba(74, 203, 217, 0.712);
    text-align: center;
    text-transform: uppercase;
    border-radius: 2%;
    width: 50vw;
    transition: 0.3s;
}
.infected:hover{
    background-color: rgba(74, 203, 217, 0.904);
}
.infected h1{
    color: rgb(255, 255, 255);
    margin: 20px 0; 
   
}
.infected p {
    color: rgba(255, 255, 255, 0.897);
    font-size: 30px;
    margin: 15px 0;
}
.infected span{
    font-weight: bold;
}
@media (max-width: 800px){
    .infected{
        margin: 10px auto 10px auto;
        padding: 10px;
        width: 98vw;
    }
    .infected h1{
        margin: 10px 0; 
        font-size: 18px;
       
    }
  }